import { useContext, useState, useRef } from "react";
import CartContext from "../../store/cart-context";
import Modal from "../UI/Modal";
import CartItem from "./CartItem";
import classes from "./Cart.module.css";

const Cart = (props) => {
  const nameRef = useRef();
  const phoneRef = useRef();
  const addressRef = useRef();

  const [showCheckout, setShowCheckout] = useState(false);

  const cartCtx = useContext(CartContext);

  const totalAmount = `₹${cartCtx.totalAmount.toFixed(2)}`;
  const hasItems = cartCtx.items.length > 0;

  const cartItemRemoveHandler = (id) => {
    cartCtx.removeItem(id);
  };
  const cartItemAddHandler = (item) => {
    cartCtx.addItem({ ...item, amount: 1 });
  };

  const cartItems = (
    <ul className={classes["cart-items"]}>
      {cartCtx.items.map((item) => (
        <CartItem
          key={item.id}
          id={item.id}
          name={item.name}
          amount={item.amount}
          price={item.price}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemAddHandler.bind(null, item)}
        />
      ))}
    </ul>
  );

  const displayCheckout = () => {
    setShowCheckout(true);
  };

  const submitHandler = (event, close) => {
    event.preventDefault();
    const name = nameRef.current.value;
    const phone = phoneRef.current.value;
    const address = addressRef.current.value;
    cartCtx.placeOrder(name, phone, address);
    close();
  };

  return !showCheckout ? (
    <Modal onClose={props.onClose}>
      {cartItems}
      <div className={classes.total}>
        <span>Total Amount</span>
        <span>{totalAmount}</span>
      </div>
      <div className={classes.actions}>
        <button className={classes["button--alt"]} onClick={props.onClose}>
          Close
        </button>
        {hasItems && (
          <button className={classes.button} onClick={displayCheckout}>
            Order
          </button>
        )}
      </div>
    </Modal>
  ) : (
    <Modal
      onClose={() => {
        setShowCheckout(false);
      }}
    >
      <h2 className={classes.heading}>Customer Details</h2>
      <form
        onSubmit={(event) => {
          submitHandler(event, props.onClose);
        }}
      >
        <div className={classes["control-group"]}>
          <div className={classes["form-control"]}>
            <label htmlFor="name">Name</label>
            <input type="text" ref={nameRef} id="name" required />
          </div>
          <div className={classes["form-control"]}>
            <label htmlFor="phone">Contact No.</label>
            <input type="number" ref={phoneRef} id="phone" required />
          </div>
        </div>
        <div className={classes["control-group"]}>
          <div className={classes["form-control"]}>
            <label htmlFor="address">Address</label>
            <textarea
              rows="1"
              ref={addressRef}
              type="text"
              id="address"
              required
            />
          </div>
        </div>
        <div className={classes.actions}>
          <button
            className={classes["button--alt"]}
            onClick={() => {
              setShowCheckout(false);
            }}
          >
            Close
          </button>
          <button type="submit" className={classes.button}>
            Confirm
          </button>
        </div>
      </form>
    </Modal>
  );
};

export default Cart;
