import { useReducer, useEffect } from "react";

import CartContext from "./cart-context";

const defaultCartState = {
  items: [],
  totalAmount: 0,
};

const cartReducer = (state, action) => {
  if (action.type === "SET") {
    return action.cart;
  }
  if (action.type === "PLACE") {
    fetch(
      "https://react-practice-e960e-default-rtdb.asia-southeast1.firebasedatabase.app/order.json",
      {
        method: "PATCH",
        body: JSON.stringify({
          [action.details.name]: {
            name: action.details.name,
            phone: action.details.phone,
            address: action.details.address,
            order: action.cart,
          },
        }),
        headers: { "Content-Type": "application/json" },
      }
    )
      .then((res) => {
        console.log(res);
        fetch(
          "https://react-practice-e960e-default-rtdb.asia-southeast1.firebasedatabase.app/cart.json",
          {
            method: "DELETE",
          }
        );
      })
      .then(() => {
        return {
          items: [],
          totalAmount: 0,
        };
      });
  }
  if (action.type === "ADD") {
    const updatedTotalAmount =
      state.totalAmount + action.item.price * action.item.amount;

    const existingCartItemIndex = state.items.findIndex(
      (item) => item.id === action.item.id
    );

    const existingCartItem = state.items[existingCartItemIndex];
    let updatedItems;

    if (existingCartItem) {
      const updatedItem = {
        ...existingCartItem,
        amount: existingCartItem.amount + action.item.amount,
      };
      updatedItems = [...state.items];
      updatedItems[existingCartItemIndex] = updatedItem;
    } else {
      updatedItems = state.items.concat(action.item);
    }
    fetch(
      "https://react-practice-e960e-default-rtdb.asia-southeast1.firebasedatabase.app/cart.json",
      {
        method: "PUT",
        body: JSON.stringify({
          items: updatedItems,
          totalAmount: updatedTotalAmount,
        }),
        headers: { "Content-Type": "application/json" },
      }
    );

    return {
      items: updatedItems,
      totalAmount: updatedTotalAmount,
    };
  }
  if (action.type === "REMOVE") {
    const existingCartItemIndex = state.items.findIndex(
      (item) => item.id === action.id
    );
    const existingItem = state.items[existingCartItemIndex];
    const updatedTotalAmount = state.totalAmount - existingItem.price;
    let updatedItems;
    if (existingItem.amount === 1) {
      updatedItems = state.items.filter((item) => {
        return item.id !== action.id;
      });
    } else {
      const updatedItem = {
        ...existingItem,
        amount: existingItem.amount - 1,
      };
      updatedItems = [...state.items];
      updatedItems[existingCartItemIndex] = updatedItem;
    }

    fetch(
      "https://react-practice-e960e-default-rtdb.asia-southeast1.firebasedatabase.app/cart.json",
      {
        method: "PUT",
        body: JSON.stringify({
          items: updatedItems,
          totalAmount: updatedTotalAmount,
        }),
        headers: { "Content-Type": "application/json" },
      }
    );

    return {
      items: updatedItems,
      totalAmount: updatedTotalAmount,
    };
  }
  return defaultCartState;
};

const CartProvider = (props) => {
  useEffect(() => {
    const getCart = async () => {
      const response = await fetch(
        "https://react-practice-e960e-default-rtdb.asia-southeast1.firebasedatabase.app/cart.json"
      );
      const cartData = await response.json();
      if (cartData) {
        dispatchCartAction({ type: "SET", cart: cartData });
      }
    };
    getCart();
  }, []);

  const [cartState, dispatchCartAction] = useReducer(
    cartReducer,
    defaultCartState
  );
  const addItemFromCartHandler = (item) => {
    dispatchCartAction({ type: "ADD", item: item });
  };

  const removeItemFromCartHandler = (id) => {
    dispatchCartAction({ type: "REMOVE", id: id });
  };

  const placeOrderHandler = (name, phone, address) => {
    console.log("Place Order");
    dispatchCartAction({
      type: "PLACE",
      details: { name, phone, address },
      cart: cartState,
    });
  };

  const cartContext = {
    items: cartState.items,
    totalAmount: cartState.totalAmount,
    addItem: addItemFromCartHandler,
    removeItem: removeItemFromCartHandler,
    placeOrder: placeOrderHandler,
  };

  return (
    <CartContext.Provider value={cartContext}>
      {props.children}
    </CartContext.Provider>
  );
};

export default CartProvider;
